/* Global styles and variables */
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@500&family=Roboto:wght@500&display=swap");

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

:root {
  --primary-color: #F9CC0B;
  --secondary-color: #F9F9F9;
  --third-color: #0C1D36;
  --primary-text-color: #0C1D36; /* Updated primary text color */
  --secondary-text-color: #577592;
  --accent-color: #2294ed;
  --accent-color-dark: #F9F9F9;
}

body {
  font-family: "Poppins", sans-serif;
  color: var(--primary-text-color);
  background-color: var(--secondary-color); /* Set background color */
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  min-height: 100vh; /* Ensure the body takes the full height of the viewport */
}

p {
  font-family: "Roboto", sans-serif;
  color: var(--secondary-text-color);
  line-height: 1.4rem;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

.flex {
  display: flex;
  align-items: center;
}

.container {
  max-width: 1180px;
  margin: 0 auto; /* Center align container */
  padding: 0 20px; /* Add padding on the sides */
  overflow: hidden;
}

nav {
  background-color: var(--third-color); /* Updated background color */
  box-shadow: 0 0 35px #bbd0e2;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 99;
}

.main-nav {
  justify-content: space-between;
  padding: 8px 0; /* Adjust vertical padding */
}

.company-logo img {
  width: 100px; /* Adjust logo size */
  height: auto;
}

.nav-toggle {
  display: none; /* Hide toggle button by default */
  cursor: pointer;
}

.nav-links {
  display: flex;
  align-items: center;
  gap: 16px;
}

.hover-link {
  cursor: pointer;
  transition: color 0.3s ease;
}

.hover-link:hover {
  color: var(--primary-color); /* Updated hover color */
}

.nav-item.active {
  color: var(--primary-color); /* Updated active color */
}

.nav-item.false {
  color: var(--secondary-color); /* Updated false color */
}

.search-bar {
  display: flex;
  align-items: center;
  gap: 8px;
}

.news-input {
  width: 100%;
  max-width: 300px;
  padding: 12px;
  border-radius: 8px;
  border: 1px solid var(--primary-color);
  font-family: "Roboto", sans-serif;
  color: var(--third-color);
  font-size: small;
}

.search-button {
  background-color: var(--primary-color); /* Updated background color */
  color: var(--third-color); /* Updated text color */
  padding: 10px 20px; /* Increase padding */
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-family: "Roboto", sans-serif;
}

.search-button:hover {
  background-color: var(--accent-color-dark);
}

main {
  padding: 20px;
  margin-top: 80px;
  flex: 1; /* Ensure main content takes up available space */
}

.cards-container {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
}

.card {
  width: calc(33.33% - 20px); /* Adjust card width for responsive layout */
  height: 400px; /* Fixed height for consistent card sizes */
  box-shadow: 0 0 4px var(--accent-color-dark);
  border-radius: 4px;
  cursor: pointer;
  background-color: var(--secondary-color); /* Updated background color */
  overflow: hidden;
  transition: transform 0.3s ease, box-shadow 0.3s ease; /* Smooth transition for transform and box-shadow */
  display: flex; /* Ensure flex behavior for consistent heights */
  flex-direction: column; /* Arrange content vertically */
}

.card:hover {
  box-shadow: 1px 1px 8px #d4ecff;
  transform: translateY(-5px) scale(1.05); /* Move card slightly up and scale it */
}

.card-header {
  flex: 1; /* Allow header to expand */
}

.card-header img {
  width: 100%;
  height: 180px;
  object-fit: cover;
}

.card-content {
  padding: 12px;
  flex: 1; /* Allow content to expand */
  display: flex;
  flex-direction: column;
  justify-content: space-between; /* Distribute space evenly */
}

.news-source {
  margin-top: 12px; /* Adjust spacing */
  color: var(--secondary-text-color);
  font-size: 14px; /* Adjust font size */
}

.card-content h3 {
  margin: 0;
  font-size: 1.2em; /* Adjust as needed */
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2; /* Show maximum 2 lines */
  -webkit-box-orient: vertical;
  line-height: 1.4em; /* Adjust line height if needed */
}

.card-content .news-desc {
  margin: 10px 0;
  color: #555;
  font-size: 0.9em; /* Decrease font size for the description */
  display: -webkit-box;
  -webkit-line-clamp: 3; /* Show maximum 3 lines */
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
}

.blog-meta {
  display: flex;
  gap: 10px; /* Add space between items */
  font-size: 0.75em; /* Decrease font size */
}

.blog-meta p {
  margin: 0;
}

.blog-author {
  font-weight: bold;
}

.blog-date,
.blog-read-time {
  font-style: italic;
  font-size: 0.85em; /* Decrease font size */
}

.nav-links .flex {
  display: flex;
  list-style: none;
  padding: 0;
  margin: 0;
}

.nav-links .nav-item {
  margin-right: 10px; /* Adjust spacing between items */
  cursor: pointer;
}

.nav-links .space {
  width: 10px; /* Adjust width for space */
}

/* Footer styles */
.footer {
  background-color: var(--third-color); /* Footer background color */
  color: var(--secondary-color); /* Footer text color */
  text-align: center;
  padding: 20px 0; /* Adjust padding as needed */
  width: 100%;
  position: relative; /* Ensure the footer position is relative */
  margin-top: auto; /* Push the footer to the bottom if the content is short */
}

.footer .container {
  max-width: 1180px;
  margin: 0 auto;
  padding: 0 20px;
}

.footer p {
  margin: 0;
  font-family: "Roboto", sans-serif;
  color: var(--secondary-color);
}

/* Floating back to top button */
.back-to-top-floating {
  position: fixed;
  bottom: 20px;
  left: 20px;
  background-color: var(--primary-color); /* Button background color */
  color: var(--third-color); /* Button text color */
  border: none;
  border-radius: 50%;
  padding: 10px 15px;
  font-size: 24px;
  cursor: pointer;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /* Add shadow for better visibility */
  transition: background-color 0.3s ease;
}

.back-to-top-floating:hover {
  background-color: var(--accent-color); /* Change background on hover */
}

/* Media Queries for Responsive Design */
@media (max-width: 768px) {
  .company-logo img {
    width: 80px; /* Adjust logo size for smaller screens */
  }

  .nav-toggle {
    display: block; /* Show toggle button for mobile */
    margin-left: auto; /* Align to the right */
    cursor: pointer;
  }

  .nav-links {
    flex-direction: column;
    position: absolute;
    top: 64px; /* Adjust top position */
    left: 0;
    right: 0;
    background-color: var(--secondary-color); /* Updated background color */
    box-shadow: 0 0 4px #bbd0e2;
    padding: 12px 0; /* Adjust padding */
    display: none;
  }

  .nav-links.show {
    display: flex;
  }

  .nav-links .nav-item {
    padding: 10px 20px; /* Adjust padding */
    border-bottom: 1px solid var(--third-color); /* Updated border color */
  }

  .search-bar {
    width: 100%;
    padding: 0 20px; /* Adjust padding */
    margin-top: 10px; /* Add margin for spacing */
  }

  .cards-container {
    flex-direction: column;
  }

  .card {
    width: 100%;
  }

  .card-header img {
    height: auto; /* Maintain aspect ratio */
  }
}
